<template>
  <div class="">

        <table class="table data-table">
          <thead>
            <tr>
              <th scope="col" class="text-center">序号</th>
              <th scope="col">名称</th>
              <th scope="col" class="text-center">创建时间</th>
            </tr>
          </thead>
