            <tr v-for="(t, i) in tags" :key="t.id">
              <th scope="row" class="text-center">{{ i + 1 }}</th>
              <td>{{ t.named }}</td>
              <td class="text-center">{{ t.add_time }}</td>
              <!-- td>
                <i @click="option(t.id, 'edit')">
                  <pencil></pencil>
                </i>
                <i @click="option(t.id, 'trash')">
                  <remove></remove>
                </i>
              </td -->
            </tr>
          </tbody>
        </table>
    <tag-update ref="tagUpdate"></tag-update>
  </div>
</template>

<script>
import service from '@/service'
import Pencil from '@/components/Option/Pencil.vue'
import Remove from '@/components/Option/Remove.vue'
import TagUpdate from './TagUpdate.vue'

export default {
  name: 'TagViewDataTable',
  components: {
    Pencil, Remove,
    TagUpdate,
  },
  data() {
    return {
      tags: service.tags
    }
  },
  methods: {
    option(id, option) {
      if (option == 'trash') {
        alert('删除 ID = ' + id)
      } else if (option == 'edit') {
        
      }
    }
  }
}
</script>

